    reporter = new JestAIReporter(globalConfig, options);
    consoleLogSpy = jest.spyOn(console, 'log').mockImplementation();
    
    if (!fs.existsSync(testOutputDir)) {
      fs.mkdirSync(testOutputDir, { recursive: true });
    }
